/**
 * @packageDocumentation
 * @module Content
 */
import * as React from 'react';
import { Content, ContentProps } from '@ali/4ever-cangjie';
import {
  theme,
} from '@ali/4ever-bamboo';

// 历史原因：部分默认样式写在 bi 这一层
const { defaultKeyTypographyStyle } = theme;

export type BiContentProps = ContentProps;

/**
 * 编辑器的内容区域，编辑的数据、光标、选区等，均在此区域内渲染。
 */
export default class BiContent extends React.Component<BiContentProps> {
  static defaultProps: Partial<BiContentProps> = {
    style: {},
  };

  render() {
    const {
      style,
      ...rest
    } = this.props;

    const children = (
      <Content
        {...rest}
        defaultStyle={defaultKeyTypographyStyle}
        style={style}
      />
    );

    return children;
  }
}
